<template>
  <div v-loading="loading" element-loading-text="">
    <div class="handle-box">
      <div class="btn">
        <el-input
          v-model="renter_name"
          placeholder="/"
          class="input-with-select"
          @keyup.enter.native="search(renter_name)"
        >
          <el-select
            v-model="type"
            placeholder=""
            @change="changeType"
            slot="prepend"
            style="width: 150px;"
          >
            <el-option
              v-for="item in typeList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-button slot="append" icon="el-icon-search" @click="search(renter_name)"></el-button>
        </el-input>
      </div>
    </div>

    <!--  -->
    <el-table
      :data="tableData"
      empty-text=""
      border
      :header-cell-style="{background:'#f0f0f0'}"
    >
      <el-table-column prop="id" label="ID"></el-table-column>
      <el-table-column prop="snapshot.name" label=""></el-table-column>
      <el-table-column prop="typeString" label=""></el-table-column>
      <el-table-column prop="snapshot" label="">
        <template slot-scope="scope">
          <div v-if="scope.row.snapshot">
            <el-popover placement="top-start" title trigger="click">
              <img :src="scope.row.snapshot.href" style="max-width:800px;max-height:800px;" />
              <img
                slot="reference"
                :src="scope.row.snapshot.href"
                style="max-width:180px;max-height:80px;"
              />
            </el-popover>
          </div>
          <div v-else>
            <span>----</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="snapshot.phone" label="" width="150px"></el-table-column>
      <el-table-column prop="snapshot.card_number" label="" width="180px"></el-table-column>
      <el-table-column prop="address.address" label="" width="250px"></el-table-column>
      <el-table-column prop="expireTime" label="" width="180px"></el-table-column>
      <el-table-column prop="room" width="120px" label=""></el-table-column>
      <el-table-column prop="state" width="120px" label="">
        <template slot-scope="scope">
          <div v-if="scope.row.state == 1">
            <span></span>
          </div>
          <div v-if="scope.row.state == 2">
            <span style="color: green;"></span>
          </div>
          <div v-if="scope.row.state == 3">
            <span style="color: red;"></span>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-dropdown>
            <el-button type="primary">
              
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <el-button
                  size="mini"
                  type="primary"
                  @click="handleLogs(scope.$index, scope.row)"
                ></el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button
                  size="mini"
                  type="primary"
                  v-if="scope.row.state == 1"
                  @click="handleAudit(scope.$index, scope.row)"
                ></el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button
                  size="mini"
                  type="primary"
                  v-if="isShow"
                  @click="openServe(scope.$index, scope.row)"
                ></el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button
                  size="mini"
                  type="success"
                  @click="openedServe(scope.$index, scope.row)"
                ></el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button
                  type="success"
                  size="mini"
                  @click="handleFace(scope.$index, scope.row)"
                ></el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button
                  type="danger"
                  size="mini"
                  @click="handleForbidden(scope.$index, scope.row)"
                ></el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)"></el-button>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <div class="block">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="pageSize"
        layout="sizes, prev, pager, next, jumper"
        :total="totalPage"
        @size-change="handleSizeChange"
      ></el-pagination>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogLogs">
      <div class="box">
        <el-table :data="logsData" border :header-cell-style="{background:'#f0f0f0'}">
          <el-table-column prop="id" label="ID"></el-table-column>
          <el-table-column prop="number" label=""></el-table-column>
          <el-table-column prop="time" label=""></el-table-column>
          <el-table-column prop="direction" label="">
            <template slot-scope="scope">
              <span v-if="scope.row.direction == 1"></span>
              <span v-else-if="scope.row.direction == 2"></span>
            </template>
          </el-table-column>
          <el-table-column prop="image" label="">
            <template slot-scope="scope">
              <div v-if="scope.row.image">
                <el-popover placement="top-start" title trigger="click">
                  <img :src="scope.row.image" style="max-width:800px;max-height:800px;" />
                  <img
                    slot="reference"
                    :src="scope.row.image"
                    style="max-width:180px;max-height:80px;"
                  />
                </el-popover>
              </div>
              <div v-else>
                <span>----</span>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="block">
        <el-pagination
          @current-change="handleCurrentLogs"
          :current-page.sync="currentLogsPage"
          :page-sizes="[10, 20, 30, 40, 50]"
          :page-size="pageSizeLogs"
          layout="sizes, prev, pager, next, jumper"
          :total="totalLogsPage"
          @size-change="handleSizeLogs"
        ></el-pagination>
      </div>
    </el-dialog>

    <el-dialog :visible.sync="dialogAudit" title="" width="20%" align="center">
      <div style="font-size: 20px; margin-bottom: 30px;"></div>
      <span>
        <el-button type="primary" @click="toAudit"></el-button>
        <el-button type="danger" @click="unAudit"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      :visible.sync="dialogDel"
      title=""
      width="20%"
      align="center"
      :close-on-click-modal="false"
    >
      <div style="font-size: 20px; margin-bottom: 30px;"></div>
      <span>
        <el-button type="primary" @click="toDel"></el-button>
        <el-button type="danger" @click="dialogDel = false"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      :visible.sync="dialogForbidden"
      title=""
      width="20%"
      align="center"
      :close-on-click-modal="false"
    >
      <div style="font-size: 20px; margin-bottom: 30px;"></div>
      <span>
        <el-button type="primary" @click="forbiddenFace"></el-button>
        <el-button type="danger" @click="dialogForbidden = false"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      :visible.sync="dialogFace"
      title=""
      width="20%"
      align="center"
      :close-on-click-modal="false"
    >
      <div style="font-size: 20px; margin-bottom: 30px;"></div>
      <span>
        <el-button type="primary" @click="pushFace"></el-button>
        <el-button type="danger" @click="dialogFace = false"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogOpenServe"
      width="20%"
      align="center"
      :close-on-click-modal="false"
    >
      <div style="font-size: 20px; margin-bottom: 30px;">
        <el-select v-model="title" placeholder="" @change="serveChange">
          <el-option
            v-for="item in serviceLists"
            :key="item.id"
            :label="item.title"
            :value="item.id"
          ></el-option>
        </el-select>
      </div>

      <div>
        <el-button type="primary" @click="toConfirm"></el-button>
        <el-button type="danger" @click="dialogOpenServe = false"></el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogOpenedServe">
      <div class="box">
        <el-table :data="serviceList" border :header-cell-style="{background:'#f0f0f0'}">
          <el-table-column prop="id" label="ID"></el-table-column>
          <el-table-column prop="name" label=""></el-table-column>
          <el-table-column prop="time" label="()"></el-table-column>
          <el-table-column prop="expireTime" label=""></el-table-column>
        </el-table>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/index.js";
import { log } from "util";

export default {
  data() {
    return {
      loading: true,
      form: {
        identity: "",
      },

      tableData: [], // 
      currentPage: 1, // 
      pageSize: 10,
      totalPage: 0,

      dialogLogs: false, // 
      logsData: [],
      currentLogsPage: 1, // --
      pageSizeLogs: 10,
      totalLogsPage: 0,
      dialogAudit: false, // 
      renter_id: "", // id
      renter_name: "", // 
      id: "", // id
      dialogDel: false,
      dialogOpenServe: false,
      dialogOpenedServe: false,
      serviceLists: [],
      serviceList: [],
      title: "",
      product_id: "",
      serveForm: {
        user_id: "",
        area_id: "",
        product_id: "",
        address_id: "",
      },
      user_id: "",
      addresses_id: "",
      face_id: "",
      username: localStorage.getItem("username"),
      isShow: false,
      type: 2, // 
      typeList: [
        {
          // 
          value: 1,
          label: "",
        },
        {
          value: 2,
          label: "",
        },
      ],
      typeDisabled: false,
      dialogForbidden: false,
      forbidden_id: "",
      dialogFace: false,
      openFace_id: "",
      member_type: "",
      card_number: "",
      check: "",
      family: "",
    };
  },
  mounted() {
    this.getAllRent();
    if (this.username == "admin") {
      this.isShow = true;
    }
  },
  methods: {
    getAllRent() {
      var self = this;
      API.households(self.currentPage, self.pageSize)
        .then((res) => {
          console.log(res);
          res.data.forEach((item) => {
            if (item.expireTime) {
              item.expireTime = item.expireTime.slice(0, 10);
              // console.log(item.expireTime);
            }
          });
          self.tableData = res.data;
          self.totalPage = res.total;
          self.loading = false;
        })
        .catch((err) => {
          self.loading = false;
          console.log(err);
        });
    },
    changeType(val) {
      var self = this;
      self.typeDisabled = true;
      self.renter_name = "";
      self.currentPage = 1;
      self.getAllRent();
    },

    // 
    search() {
      var self = this;
      self.currentPage = 1;
      self.pageSize = 10;
      if (self.type == 1) {
        var keyword = self.renter_name;
        API.searchAddress(self.currentPage, self.pageSize, keyword).then(
          (res) => {
            self.tableData = res.data;
            self.totalPage = res.total;
            self.$message.success("");
          }
        );
      }
      if (self.type == 2) {
        var name = self.renter_name;
        API.searchHousehold(self.currentPage, self.pageSize, name).then(
          (res) => {
            self.tableData = res.data;
            self.totalPage = res.total;
            self.$message.success("");
          }
        );
      }
    },
    handleFace(index, row) {
      var self = this;
      self.openFace_id = row.id;
      self.dialogFace = true;
    },
    pushFace() {
      var self = this;
      API.pushFace(self.openFace_id).then((res) => {
        self.dialogFace = false;
        self.$message.success("");
      });
    },
    handleForbidden(index, row) {
      var self = this;
      console.log(row);
      self.forbidden_id = row.id;
      self.dialogForbidden = true;
    },
    forbiddenFace() {
      var self = this;
      API.failFace(self.forbidden_id).then((res) => {
        self.dialogForbidden = false;
        self.$message.success("");
      });
    },

    // 
    handleAudit(index, row) {
      var self = this;
      console.log(row);
      self.renter_id = row.id;
      self.member_type = row.type;
      self.card_number = row.snapshot.card_number;
      self.check = row.check;
      self.dialogAudit = true;
      if (self.check == 0) {
        self.$message.warning("!");
        self.dialogAudit = false;
      } else if (self.check == 2) {
        self.$message.warning("!");
        self.dialogAudit = false;
      }
    },
    toAudit() {
      var self = this;
      if (self.check == 1) {
        if (self.member_type == 3) {
          API.auditFamily(self.renter_id, 2, self.card_number).then((res) => {
            self.$message.success("");
            self.dialogAudit = false;
            self.getAllRent();
          });
        } else {
          API.audit(self.renter_id, 2, 1).then((res) => {
            self.$message.success("");
            self.dialogAudit = false;
            self.getAllRent();
          });
        }
      }
    },
    unAudit() {
      var self = this;
      if (self.check == 1) {
        if (self.member_type == 3) {
          API.audit(self.renter_id, 3, self.card_number).then((res) => {
            self.$message.success("");
            self.dialogAudit = false;
            self.getAllRent();
          });
        } else {
          API.audit(self.renter_id, 3, 1).then((res) => {
            self.$message.success("");
            self.dialogAudit = false;
            self.getAllRent();
          });
        }
      }
    },
    serveChange(val) {
      var self = this;
      self.serveForm.product_id = val;
      console.log(self.serveForm.product_id);
    },
    // 
    openServe(index, row) {
      var self = this;
      console.log("openServe", index);
      console.log("openServe", row);
      self.dialogOpenServe = true;
      self.serveForm = {
        user_id: row.user_id,
        areas_id: row.address.area_id,
        product_id: self.product_id,
        addresses_id: row.address_id,
      };
      API.buys().then((res) => {
        console.log(res);
        self.serviceLists = res.data;
      });
    },
    // 
    openedServe(index, row) {
      var self = this;
      self.dialogOpenedServe = true;
      console.log("openedServe", row);
      self.user_id = row.user_id;
      self.addresses_id = row.address_id;
      API.userServes(self.user_id, self.addresses_id).then((res) => {
        console.log(res);
        self.serviceList = res;
      });
    },

    toConfirm() {
      var self = this;
      console.log(self.serveForm);
      API.setProduct(self.serveForm).then((res) => {
        console.log("toConfirm", res);
        self.$message.success("");
        self.dialogOpenServe = false;
        self.title = "";
      });
    },
    handleLogs(index, row) {
      var self = this;
      self.dialogLogs = true;
      self.face_id = row.face_id;
      console.log(row);
      self.getFaceLogs();
    },

    // 
    getFaceLogs() {
      var self = this;
      API.faceLogs(self.currentLogsPage, self.pageSizeLogs, self.face_id).then(
        (res) => {
          console.log("getFaceLogs", res);
          self.logsData = res.data;
          self.totalLogsPage = res.total;
        }
      );
    },

    handleDel(index, row) {
      var self = this;
      self.id = row.id;
      self.card_number = row.snapshot.card_number;
      self.family = row.type;
      self.dialogDel = true;
      console.log(row);
    },
    toDel() {
      var self = this;
      if (self.family == 3) {
        API.delHousehold(self.id, self.card_number).then((res) => {
          self.$message.success("");
          self.dialogDel = false;
          self.getAllRent();
        });
      } else {
        API.delHousehold(self.id, 1).then((res) => {
          self.$message.success("");
          self.dialogDel = false;
          self.getAllRent();
        });
      }
    },

    // 
    handleCurrentChange(val) {
      var self = this;
      self.currentPage = val;
      if (self.renter_name) {
        switch (self.type) {
          case 1:
            var keyword = self.renter_name;
            API.searchAddress(val, self.pageSize, keyword).then((res) => {
              self.tableData = res.data;
              self.totalPage = res.total;
            });
            break;
          case 2:
            var name = self.renter_name;
            API.searchHousehold(val, self.pageSize, name).then((res) => {
              self.tableData = res.data;
              self.totalPage = res.total;
            });
        }
      } else {
        API.households(val, self.pageSize).then((res) => {
          self.tableData = res.data;
          self.totalPage = res.total;
        });
      }
    },

    // 
    handleSizeChange(val) {
      var self = this;
      self.pageSize = val;
      if (self.renter_name) {
        switch (self.type) {
          case 1:
            var keyword = self.renter_name;
            API.searchAddress(1, val, keyword).then((res) => {
              self.tableData = res.data;
              self.totalPage = res.total;
            });
            break;
          case 2:
            var name = self.renter_name;
            API.searchHousehold(1, val, name).then((res) => {
              self.tableData = res.data;
              self.totalPage = res.total;
            });
        }
      } else {
        API.households(self.currentPage, val).then((res) => {
          self.tableData = res.data;
          self.totalPage = res.total;
          self.currentPage = 1;
        });
      }
    },

    // 
    handleCurrentLogs(val) {
      var self = this;
      self.currentLogsPage = val;
      API.faceLogs(val, self.pageSizeLogs, self.face_id).then((res) => {
        self.logsData = res.data;
      });
    },

    handleSizeLogs(val) {
      var self = this;
      self.pageSizeLogs = val;
      API.faceLogs(self.currentLogsPage, val, self.face_id).then((res) => {
        self.logsData = res.data;
      });
    },
  },
};
</script>

<style>
</style>

<style scoped>
.service {
  display: flex;
  flex-wrap: wrap;
}

.service-item {
  margin: 10px;
  padding: 0 10px;
}
</style>